/* Reset default styles */

/* Fullscreen container with background */
.forgot-password-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: url("./bg.jpg") no-repeat center center/cover;
}

/* Forgot password container */
.forgot-password-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.forgot-password-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f6f8;
}

.forgot-password-card {
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  width: 400px;
}

.forgot-password-title {
  font-size: 24px;
  margin-bottom: 10px;
  text-align: center;
}

.forgot-password-description {
  font-size: 14px;
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
}

.form-input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.submit-button {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  border: none;
  color: #fff;
  font-size: 15px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.back-to-login-button {
  display: block;
  margin-top: 15px;
  text-align: center;
  color: #007bff;
  text-decoration: none;
  font-size: 14px;
}

.status-message {
  color: red;
  font-size: 14px;
  text-align: center;
  margin-bottom: 10px;
}

/* Pulse Animation */
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.08); }
  100% { transform: scale(1); }
}

.pulse-animation {
  animation: pulse 0.6s ease;
}

/* Forgot password card */
.forgot-password-card {
  width: 420px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(9px);
  color: #fff;
  border-radius: 12px;
  padding: 30px 40px;
  text-align: center;
}

/* Title styling */
.forgot-password-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Description */
.forgot-password-description {
  font-size: 14px;
  margin-bottom: 20px;
}

/* Input field styling */
.form-group {
  width: 100%;
  text-align: left;
  margin-bottom: 15px;
}

.form-label {
  font-size: 16px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
}

.form-input {
  width: 100%;
  height: 45px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 40px;
  font-size: 16px;
  color: #fff;
  padding: 15px 20px;
  outline: none;
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

/* Submit button */
.submit-button {
  width: 100%;
  height: 45px;
  background: #fff;
  border: none;
  border-radius: 40px;
  font-size: 16px;
  color: #333;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
  margin-top: 10px;
}

.submit-button:hover {
  background: #ddd;
}

/* Back to login button */
.back-to-login-button {
  display: block;
  margin-top: 15px;
  color: #fff;
  font-size: 14px;
  text-decoration: none;
  transition: color 0.3s;
}

.back-to-login-button:hover {
  text-decoration: underline;
}